<template>
	<view class="">{{ipData}}</view>
	<view class="">{{ipInfo}}</view>
</template>

<script setup lang="js">
	import {
		onMounted,
		ref
	} from 'vue';
	import http from '../../utils/http';
	const baseUrl = 'https://uapis.cn'
	const ipData = ref("")
	const ipInfo = ref("")

	async function test() {
		const res = await http(baseUrl + '/api/v1/network/myip')
		ipData.value = res.region
	}

	async function getWeather() {
		const res = await http(baseUrl + '/api/v1/misc/weather', {
			city: ipData.value,
			extended: true,
			indices: true,
			forecast: true
		})
		ipInfo.value = res
	}

	onMounted(async () => {
		await test()
		await getWeather()

	})
</script>

<style lang="scss" scoped></style>